.video{
  display: flex;
  align-items: center;
  border: 1px solid lightgray;
  margin-bottom: 0.2em;
  cursor: pointer;
  transition: transform 250ms ease-in;
  width: 50%;
}
.video.grid{
  width: 50%;
}
.video.list{
  width: 100%;
}
.video:hover{
  transform: scale(1.02);
}
.thumbnail{
  width: 40%;
}
.title,
.channel{
  margin: 0;
}
.channel{
  font-size: 0.9rem;
}